import { Label } from "semantic-ui-react";



interface Props {

  filename: string;
}


export default function DocumentViewer(props: Props) {

  const file_url = 'https://docs.google.com/document/d/1I0IAVYyotSR1gXwOiWpAvk7kbTowf-CV/edit?usp=sharing&ouid=110933002256362805095&rtpof=true&sd=true&rm=minimal';
  const docxFilePath = `/assets/documents/${props.filename}`;

  const handleDownload = () => {
    window.location.href = docxFilePath;
  };

  return (


    <div>
        <Label  as='a' onClick={handleDownload} style={{float:'right'}} color='teal' size='huge'>
               Download
            </Label>
 
    <iframe
      src={file_url}
      title="Document"
      width="100%"
      height="500px"
    />
  </div>
  );
}




